import React from 'react'
import { FaTimes } from 'react-icons/fa'
import FullNote from './FullNote'

const Ts = (props) => {
    return (
    <>
        <div className='task' id={'ts-' + props.task.id} onClick={() => props.imp(props.task.id)}>
            <h3>{props.task.text}<FaTimes style={{ color: 'gray', cursor: 'pointer' }} onClick={() => props.del(props.task.id)} /></h3>
            <p>{props.task.day}</p>
        </div>
        <FullNote task={props.task}/>
    </>
    )
}

export default Ts
