import React from 'react'
import Header from './Header'
import { FaTimes } from 'react-icons/fa'

const FullNote = (props) => {
    const saveDesc = () => {
        props.task.desc = document.getElementById('TA-' + props.task.id).value
        document.getElementById('noteToast' + props.task.id).style.display = 'none'
    }
    return (
        <div className='noteToast' id={'noteToast' + props.task.id}>
            <Header title={props.task.text} username={<FaTimes onClick={saveDesc} style={{ cursor: 'pointer' }} />} />
            <textarea id={'TA-' + props.task.id}></textarea>
        </div>
    )
}

export default FullNote
