import React from 'react'
import Header from './components/Header'
import Add from './components/Add'
import Task from './components/Task'
import { useState } from 'react'

var ind = 0

function App() {
  const username = ''
  const Months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec']
  const [tasks, setTask] = useState([])
  const addTask = () => {
    if (document.getElementById('taskInp').value == '') {
      alert("Please give the note a title")
    }
    else {
      ind++
      var d = new Date()
      setTask([...tasks, {
        id: ind,
        text: document.getElementById('taskInp').value,
        desc: '',
        day: d.getDate() + ' ' + (Months[d.getMonth()]) + ' ' + d.getFullYear() + ' at ' + d.getHours() + ':' + (d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes())
      }])
      document.getElementById('taskInp').value = ''
    }

  }

  const delTask = (id) => {
    setTask(tasks.filter((task) => task.id !== id))
  }

  const setImportant = (id) => {
    document.getElementById('noteToast' + id).style.display = 'block'
  }

  return (
    <div className="container" id='cont'>
      <Header title='Note Wall' username={username} />
      <Add placeHolder='New Note' btnText='ADD' btnClick={addTask} />
      <hr />
      <Task tasks={tasks} xBtn={delTask} imp={setImportant} />

      <div className="footer"><p>Â© Ergi Bogdani 2021</p></div>

    </div>
  );
}

export default App;
